import React from 'react';
import styled, { keyframes } from 'styled-components';

const rotate360 = keyframes`
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
`;

const noRotate = keyframes`
  from { transform: rotate(0deg); }
  to { transform: rotate(0deg); }
`;

const Container = styled.div`
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  cursor: pointer;
`;

const Panel = styled.div`
  position: absolute;
  overflow: hidden;
  height: ${props => (props.opened ? '12vh' : '3vh')};
  left: 0;
  bottom: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-shadow: ${props =>
    props.opened ? '0px -2px 2px rgba(0, 0, 0, 0.05);' : '0px -2px 2px rgba(0, 0, 0, 0.2)'};
  background-color: ${props => (props.opened ? 'rgba(0, 0, 0, 0.3)' : 'rgba(0, 0, 0, 0.1)')};
  transition: all 200ms ease-out;
`;

const Text = styled.p`
  padding: 0 3vh 0 3vh;
  color: snow;
  font-family: 'Heiti SC';
  font-size: 2vh;
  text-overflow: ellipsis;
  line-height: 3vh;
`;

const Icon = styled.div`
  color: snow;
  font-family: 'Heiti SC';
  font-size: ${props => props.opened ? '5vh' : '1.8vh'};
  display: flex;
  animation: ${props =>
    props.animated ? `${rotate360} 2s linear infinite` : `${noRotate} 2s linear infinite`};
`;

const BottomPanel = props => (
  <Container onClick={props.toggleBottomPanel}>
    <Panel {...props}>
      {props.sceneLoaded ? (
        props.opened ? (
          <Text {...props}>{props.text}</Text>
        ) : (
          <Icon {...props}></Icon>
        )
      ) : (
        <Icon {...props} animated>
          
        </Icon>
      )}
    </Panel>
  </Container>
);

export default BottomPanel;



