import React, { Component } from 'react';
import styled from 'styled-components';
import Arrow from './Arrow';
import BottomPanel from './BottomPanel';
import EndPanel from './EndPanel';

const Container = styled.div`
  position: relative;
  height: 100vh;
  user-select: none;
`;

const Image = styled.img.attrs({ useMap: '#example-map-1' })`
  height: 100vh;
`;

class Player extends Component {
  constructor(props) {
    super(props);
    this.state = {
      i: props.init,
      sceneLoaded: false,
      bottomPanelOpened: true,
      scenes: [],
    };
    this.changeScene = this.changeScene.bind(this);
    this.toggleBottomPanel = this.toggleBottomPanel.bind(this);
    this.setSceneLoadedToTrue = this.setSceneLoadedToTrue.bind(this);
    this.resizeTimeout = null;
  }

  async componentWillMount() {
    try {
      const isProd = process.env.NODE_ENV === 'production';
      const dirs = window.location.pathname.split('/');
      const path = dirs.pop();
      if (!path) {
        return;
      } else {
        const url = isProd
          ? `https://res.cloudinary.com/grebett/raw/upload/${path}/scenes.json`
          : `http://localhost:3333/${path}/scenes.json`;
        const res = await fetch(url);
        const scenes = await res.json();
        this.setState({ scenes });
      }
    } catch (e) {
      console.error(e);
    }
  }

  toggleBottomPanel() {
    this.setState({ bottomPanelOpened: !this.state.bottomPanelOpened });
  }

  changeScene(i) {
    this.setState({ i, sceneLoaded: false });
  }

  setSceneLoadedToTrue() {
    this.setState({ sceneLoaded: true });
  }

  render() {
    if (this.state.scenes.length === 0) {
      return null;
    } else {
      const initialHeight = 1050;
      const playerHeight = window.innerHeight;
      const ratio = playerHeight / initialHeight;
      const scene = this.state.scenes[this.state.i];
      const Areas = scene.areas.map((area, i) => {
        const x0 = area.x * ratio;
        const y0 = area.y * ratio;
        const x1 = x0 + area.width * ratio;
        const y1 = y0 + area.height * ratio;
        const coords = `${x0},${y0} ${x1},${y1}`;
        return (
          <area
            key={`area${i}`}
            shape="rect"
            alt={area.alt}
            aria-label={area.alt}
            title={area.alt}
            coords={coords}
            onClick={() => this.changeScene(area.to)}
            onMouseEnter={() => {
              if (area.hover) {
                this.changeScene(area.to);
              }
            }}
          />
        );
      });

      // Handles automatic scenes changes
      if (scene.auto) {
        setTimeout(() => this.setState({ i: scene.auto.to }), scene.auto.delay);
      }

      // Set back the scene to not loaded until resizing is finished.
      window.onresize = () => {
        clearTimeout(this.resizeTimeout);
        this.setState({ sceneLoaded: false });
        this.resizeTimeout = setTimeout(this.setSceneLoadedToTrue, 500);
      };

      return (
        <Container>
          <map name="example-map-1">
            {Areas}
            <area shape="default" style={{ cursor: 'default' }} alt="default" aria-label="defaut" />
          </map>
          <Image
            class="scene"
            src={scene.src}
            onLoad={() => setTimeout(() => this.setState({ sceneLoaded: true }), 500)}
          />
          {scene.arrow && (
            <Arrow
              {...scene.arrow}
              sceneLoaded={this.state.sceneLoaded}
              x={scene.arrow.x * ratio}
              y={scene.arrow.y * ratio}
              width={scene.arrow.width * ratio}
            />
          )}
          {scene.text && (
            <BottomPanel
              toggleBottomPanel={this.toggleBottomPanel}
              opened={this.state.bottomPanelOpened}
              text={scene.text}
              sceneLoaded={this.state.sceneLoaded}
            />
          )}
          {scene.end && <EndPanel html={scene.html} sceneLoaded={this.state.sceneLoaded} />}
        </Container>
      );
    }
  }
}

export default Player;



